import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { ClaimType } from 'src/app/Model/claimsConstant';
import { RoleClaimsDTO } from 'src/app/Model/roleClaimsDTO';
import { AlertService } from 'src/app/services/alert.service';
import { AuthService } from 'src/app/services/auth/auth.service';

@Component({
  selector: 'app-detail-view-roles',
  templateUrl: './detail-view-roles.component.html',
  styleUrls: ['./detail-view-roles.component.css']
})
export class DetailViewRolesComponent implements OnInit {
  form:FormGroup;
  buttonLoading:boolean;
  isEditMode: boolean;
  constructor(private _formBuilder:FormBuilder,
              private AuthService:AuthService,
              private _notificationService:AlertService) {
    this.generateForm();
   }

  ngOnInit(): void {
    console.log(this.controlArr)

    this.claimType.controls.forEach(item =>{
      console.log((item as FormGroup).controls["claimTypeName"].value)
    })
  }
  generateForm()
  {
    this.form=this._formBuilder.group({
      roleName:[null,Validators.required],
      claimType:this._formBuilder.array([
        this._formBuilder.group({
          claimTypeName:[ClaimType.Dashboard],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.SalesManagement],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.BankAccount],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.Broker],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.Outstandings],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.Transactions],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.Expenses],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.TeamMember],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.Statements],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
        this._formBuilder.group({
          claimTypeName:[ClaimType.Settings],
          claimValue:this._formBuilder.group({
            create:[true] ,
            view: [true],
            edit: [true],
            delete: [true],
          })
        }),
      ])
    })
  }
  clickSwitch(fg) {
    var isCreateEnabled = fg.controls['claimValue'].controls['create'].value;
    var isEditEnabled = fg.controls['claimValue'].controls['edit'].value;
    var isDeleteEnabled = fg.controls['claimValue'].controls['delete'].value;
    if (isCreateEnabled) {
      fg.controls['claimValue'].controls['view'].setValue(true);
    }
    if (isEditEnabled) {
      fg.controls['claimValue'].controls['view'].setValue(true);
    }
    if (isDeleteEnabled) {
      fg.controls['claimValue'].controls['view'].setValue(true);
    }

    if (!isCreateEnabled && !isEditEnabled && !isDeleteEnabled) {
      fg.controls['claimValue'].controls['view'].setValue(false);
    }
  }
  clickViewSwitch(fg: FormArray) {
    let isViewEnabled = fg.controls['claimValue'].controls['view'].value;
    if (!isViewEnabled) {
      if (fg.controls['claimValue'].controls['create'])
        fg.controls['claimValue'].controls['create'].setValue(false);
      if (fg.controls['claimValue'].controls['edit'])
        fg.controls['claimValue'].controls['edit'].setValue(false);
      if (fg.controls['claimValue'].controls['delete'])
        fg.controls['claimValue'].controls['delete'].setValue(false);
    }

  }

  get f (){
    return this.form.controls;
  }
  get claimType()
  {
    return this.form.controls.claimType as FormArray;
  }
  get controlArr()
  {
    return this.claimType.controls as FormGroup[];
  }
  convertString(value: string) {
    if (value && value.includes("_"))
      return value.split("_").join(" ")
    else
      return value
  }

  handleSubmit(formDirective: FormGroupDirective) {
    if (this.form.invalid) {
      return
    }
    this.buttonLoading = true
    // this.checkRole();
    var data = this.form.value as RoleClaimsDTO;
    if (this.isEditMode) {
      this.editRole(data, formDirective);
    }
    else {
      this.addRole(data, formDirective);
    }
  }
  editRole(data: RoleClaimsDTO, formDirective: FormGroupDirective) {
    throw new Error('Method not implemented.');
  }
  addRole(data, formDirective: FormGroupDirective) {
    this.AuthService.CreateRoleWithClaims(data).subscribe(res => {
      this.buttonLoading = false
      if (res.isSuccessfull) {
        formDirective.resetForm()
        this._notificationService.success("Role Added Successfully");
        this.AuthService.GetAllRoles();
      } else {
        this._notificationService.error("Error Occurred")
      }
    })
  }

}
