import { Component, OnInit } from '@angular/core';
import { Subject, Subscription } from 'rxjs';

import { AlertService } from 'src/app/services/alert.service';
import { User } from "src/app/Model/User";
import { UserService } from "src/app/services/user/user.service"; 



@Component({
  selector: 'app-view-user',
  templateUrl: './view-user.component.html',
  styleUrls: ['./view-user.component.css']
})
export class ViewUserComponent implements OnInit {
  search: string;
  listData: User[] = [];
  isvisible = false;
  modalTitle = "Create new User";
  UserObserSubject: Subject<User> = new Subject();
  contactSubject: Subscription;
  checked = false;
  showUser = false;
  user = new User();
  userObserSubject: any; 


  constructor(private _UserService: UserService,
    private _notification: AlertService) { }

  ngOnInit(): void {
    this._UserService.userObserve$.subscribe(res => {
      if (res) {
        this.listData = res;
      }
      this.isvisible = false;
    });
    this._UserService.GetUser();
  }
  handleCancel() {
    this.isvisible = false;
    this.showUser = false;
  }
  handleOpen() {
    this.isvisible = true;
    this.modalTitle = "New User";
    this.UserObserSubject.next(null);
    this.showUser = false;
  }
  handleEdit(data: User) {
    this.modalTitle = "Edit User";
    this.isvisible = true;
    this.showUser = false;
    this.UserObserSubject.next(data);
  }
  showSingleUser(data: User) {
    this.user = data;
    this.showUser = true;
  }
  DeleteUser(data: User) {
    this._notification.delete().then(result => {
      if (result.isConfirmed) {
        this._UserService.DeleteUser(data.id).subscribe(res => {
          if (res.isSuccessfull) {
            this._UserService.GetUser();
            this._notification.success('User Deleted sucessfully!')
          }
          else {
            this._notification.error('Error while Deleteing User!')
          }
        });
      }
      else {
        return;
      }

      this.showUser = false;
    });
  }


}
