 import { Component, OnInit } from '@angular/core';
 import { FormBuilder, FormGroup, Validators } from '@angular/forms';
 import { Subject } from 'rxjs';
 import { AlertService } from 'src/app/services/alert.service';
 import { Input } from '@angular/core';
 import { UserService } from "src/app/services/user/user.service";
 import { User } from "src/app/Model/User";
import { Role } from 'src/app/Model/Role';
import { RoleDTO } from 'src/app/Model/RoleDTO';
import { AuthService } from 'src/app/services/auth/auth.service';
import { BranchDTO,BranchWithDetail } from '../../branch/view-branch/view-branch.component';
import { BranchService } from 'src/app/services/branch/branch.service';

@Component({
  selector: 'app-detailview-user',
  templateUrl: './detailview-user.component.html',
  styleUrls: ['./detailview-user.component.css']
})
export class DetailviewUserComponent implements OnInit {
  @Input() userSubject:Subject<User>;

  form:FormGroup;
  UserId:string;
  isEditMode=false;
  loading=false;
  branches:BranchDTO[] =[];
  Roles:RoleDTO[]=[];
  constructor(private fb:FormBuilder,
    private _notification:AlertService,
    private _userService:UserService,
    private _branchService:BranchService,
    private _authservice:AuthService) {
      this.form=fb.group({
        firstname:[null, [Validators.required]],
        lastname:[null, [Validators.required]],
        email:[null, [Validators.required]],
        primaryPhone:[null, [Validators.required]],
        address:[null, [Validators.required]],
        city:[null, [Validators.required]],
        country:[null, [Validators.required]],
        nationality:[null, [Validators.required]],
        dateofbirth:[Date(), [Validators.required]],
        gender:[null, [Validators.required]],
        marrigestatus:[null, [Validators.required]],
        role:[null, [Validators.required]],
        jobtitle:[null, [Validators.required]],
        isManager:[null,[Validators.required]],
        isAgent:[null,[Validators.required]],
        branchId:[null,[Validators.required]]
      });
    }
    get f() {return this.form.controls}
  ngOnInit(): void {
    this.userSubject.subscribe(res =>{
      if(res){
    this.form.patchValue(res);
   this.UserId=res.id;
    this.isEditMode=false;
      }
      else{
        this.form.reset();
        this.UserId=null;
        this.isEditMode=false;
      }
    });

    this._authservice.roleListObserver$.subscribe(res=>{
      this.Roles=res;
    })
    this._authservice.GetAllRoles();
    this._branchService.branchObserver$.subscribe(res=>{
      console.log(res);
      var branchWithDetail = res as BranchWithDetail;
      console.log('zz',branchWithDetail);
      if(res){

        this.branches=branchWithDetail.branches;
        // this.branches.push(...this.branches);
        console.log(this.branches,'loaded branch');
      }

    })
    this._branchService.GetBranches();
  }
  handleSubmit(){

     if(this.form.invalid){
       return
    }
     this.loading=true;
     let data = this.form.value as User;
     if(this.isEditMode){
       data.id=this.UserId;
       this._userService.UpdateUser(data).subscribe(res=>{
         this.loading=false;

         if(res.isSuccessfull){


           this._userService.GetUser();
           this._notification.success('User Update successfully!');
         }
         else{
          this._notification.error(' Error While Updating User!');
         }
       });
     }
   else{

     this.loading=true;
     this._userService.SaveUser(data).subscribe(res=>{
       if(res.dynamicResult==true){
         this.loading=false;
         this._userService.GetUser();
         this._notification.success('User Save successfully!');
       }
      else{
         this._notification.error(' Error While Saving User!');
       }

       });
     }
   }


   resetForm(){
     this.form.setValue({
      firstname:null,
      lastname:null,
      email:null,
      primaryphone:null,
      address:null,
      city:null,
      country:null,
      nationality:null,
      dateofbirth:null,
      gender:null,
      marrigestatus:null,
      tyepofuser:null,
      role:null,
      jobtitle:null
    });
  }
}
