import { BaseEntity } from "./BaseEntity";
import { Outstandings } from "./Outstandings";
import { Sales } from "./Sales";
import { Transactions } from "./Transactions";

export class User extends BaseEntity<string>{


    firstName:string;
    lastName:string;
    primaryPhone:string;
    secondaryPhone:string;
    email:string;
    password:string;
    address:string;
    city:string;
    country:string;
    nationality:string;
    dateofBirth:Date;
    gender:Gender;
    marriageStatus:MarriageStatus;
    typeofUser:TypeOfUser;
    role:string;
    jobTitle:string;
}
export enum TypeOfUser{
    Agent=1,
    Broker
}
export enum MarriageStatus{
    Single=1,
    Married,
    Divorced
}
export enum Gender
{
    Male=1,
    Female,
    Other
}
