import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs/internal/Subject';
import { Subscription } from 'rxjs/internal/Subscription';
import { Role } from 'src/app/Model/Role';
import { RoleClaimsDTO } from 'src/app/Model/roleClaimsDTO';
import { RoleDTO } from 'src/app/Model/RoleDTO';
import { AlertService } from 'src/app/services/alert.service';
import { AuthService } from 'src/app/services/auth/auth.service';
import { RoleService } from 'src/app/services/role.service';

@Component({
  selector: 'app-view-roles',
  templateUrl: './view-roles.component.html',
  styleUrls: ['./view-roles.component.css']
})
export class ViewRolesComponent implements OnInit {
  search: string;
  listData: RoleDTO[] = [];
  isvisible = false;
  modalTitle = "Create new User";
  UserObserSubject: Subject<RoleClaimsDTO> = new Subject();
  contactSubject: Subscription;
  checked = false;
  showUser = false;
  role = new Role();
  roleObserSubject: any; 


  constructor(private _UserService: AuthService,
    private _notification: AlertService) { }

  ngOnInit(): void {
    this._UserService.roleListObserver$.subscribe(res => {
      if (res) {
        this.listData = res;
      }
      this.isvisible = false;
    });
    this._UserService.GetAllRoles();
  }
  handleCancel() {
    this.isvisible = false;
    this.showUser = false;
  }
  handleOpen() {
    this.isvisible = true;
    this.modalTitle = "New Role";
    this.UserObserSubject.next(null);
    this.showUser = false;
  }
  handleEdit(data: RoleClaimsDTO) {
    this.modalTitle = "Edit User";
    this.isvisible = true;
    this.showUser = false;
    this.UserObserSubject.next(data);
  }

 


}

